<template>
  <div class="search">
    <input type="text" placeholder="search" />
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="icon"
      viewBox="0 0 1024 1024"
    >
      <path
        fill="currentColor"
        d="m795.904 750.72 124.992 124.928a32 32 0 0 1-45.248 45.248L750.656 795.904a416 416 0 1 1 45.248-45.248zM480 832a352 352 0 1 0 0-704 352 352 0 0 0 0 704z"
      ></path>
    </svg>
    <ul>
      <li
        v-for="item in SearchList"
        :key="item.id"
        :class="{ selected: selectedItem === item }"
        @click="selectItem(item)"
      >
        <img :src="item.img" />
        <span>{{ item.name }}</span>
        <p>{{ item.Shu }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "TodolistLeftSearch",

  data() {
    return {
      SearchList: [
        {
          id: 1,
          img: "data:image/png;base64,",
          name: "所有",
          Shu: 0,
        },
        {
          id: 2,
          img: "data:image/png;base64,",
          name: "今天",
          Shu: 0,
        },
        {
          id: 3,
          img: "data:image/png;base64,",
          name: "最近七天",
          Shu: 0,
        },
      ],
      selectedItem: null,
    };
  },

  mounted() {},

  methods: {
    selectItem(item) {
      this.selectedItem = item;
      this.$bus.$emit("selectItem", item.name);
    },
  },
};
</script>

<style lang="css" scoped>
.search {
  width: 90%;
  height: 200px;
  position: relative;
  border-bottom: 1px solid #bbb5ac;
  margin: auto;
}
input {
  width: 158px;
  height: 25px;
  margin-top: 20px;
  margin-left: 10px;
  border-radius: 10px;
  border: none;
  background-color: #e2e3e2;
  padding: 5px;
}

.icon {
  width: 14px;
  height: 14px;
  position: absolute;
  top: 30px;
  left: 155px;
}

li {
  width: 170px;
  height: 32px;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 10px;
  border-left: 3px solid transparent;
  cursor: pointer;
}
li:hover {
  background-color: rgb(231, 244, 255);
}
.selected {
  background-color: rgb(231, 244, 255);
  border-left-color: aqua;
}
img {
  width: 18px;
  height: 18px;
  float: left;
  margin-top: 7px;
}
span {
  float: left;
  margin-top: 5px;
  margin-left: 10px;
}
p {
  float: right;
  margin-top: 5px;
  margin-right: 8px;
}
</style>