<template>
  <div class="box">
    <ul>
      <li v-for="(item, index) in list" :key="index">
        <input
          type="checkbox"
          name=""
          id=""
          :class="item.flag ? 'active' : ''"
        />
        <span> {{ item.title }}</span>
        <span id="data">
          <el-date-picker
            id="dateinp"
            v-model="item.create_time"
            type="date"
            placeholder="-"
          >
          </el-date-picker>
        </span>
        <i class="el-icon-delete" @click="del(index)"></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "TodolistRightMain",

  data() {
    return {
      list: [],
    };
  },
  mounted() {
    this.$bus.$on("addpush", this.addpush);
    const storedList = localStorage.getItem("list");
    if (storedList) {
      this.list = JSON.parse(storedList);
    }
  },
  methods: {
    addpush(msg) {
      console.log(msg);
      this.list = msg;
      this.saveData();
    },

    del(index) {
      this.list.splice(index, 1);
      this.saveData();
    },
    saveData() {
      localStorage.setItem("list", JSON.stringify(this.list));
    },
  },
};
</script>
<style lang="css" scoped>
.box {
  width: 900px;
  height: 450px;
  overflow-y: hidden;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none;
}
li {
  width: 800px;
  height: 50px;
  background-color: white;
  border-radius: 10px;
  margin-left: 50px;
  margin-top: 20px;
  position: relative;
}

li input {
  float: left;
  margin-left: 20px;
  margin-top: 20px;
}

li span {
  float: left;
  margin-left: 10px;
  margin-top: 5px;
  font-size: 14px;
}

.block {
  float: left;
  width: 200px;
  height: 50px;
}
i {
  float: right;
  margin-right: 20px;
  margin-top: 10px;
}

#data {
  position: absolute;
  top: 10px;
  left: 22px;
}

.active {
  text-decoration: line-through !important;
}
</style>