<template>
    <div class="box">
        <div class="increase">
         <input type="text" placeholder="添加任务"
    v-model="inputValue"
    @focus="startAnimation"
    @blur="resetProgress"
    @keyup.enter="addlist()"
         >
         <svg data-v-a22cce3a="" xmlns="http://www.w3.org/2000/svg" class="icon" viewBox="0 0 1024 1024"><path fill="currentColor" d="M480 480V128a32 32 0 0 1 64 0v352h352a32 32 0 1 1 0 64H544v352a32 32 0 1 1-64 0V544H128a32 32 0 0 1 0-64h352z"></path></svg>
      <el-progress class="progress" :percentage="progressValue" color="#1089ff" :show-text="false" :stroke-width="barHeight"></el-progress>
        </div>
    </div>
</template>

<script>
export default {
    name: 'TodolistRightPush',

    data() {
        return {
        inputValue: "",
        progressValue: 0,
        timer: null,
        barHeight:1
        };
    },

    mounted() {
        
    },

    methods: {
        startAnimation() {
   clearInterval(this.timer); 
   this.progressValue = 0; 
   this.timer = setInterval(() => {
    if (this.progressValue < 100) {
     this.progressValue += 10; 
    } else {
     clearInterval(this.timer); 
    }
   }, 100);
  },
  resetProgress() {
   clearInterval(this.timer); 
   this.progressValue = 0; 
  },
     addlist(){
        this.$bus.$emit('addlist',this.inputValue)
     }
    },
};
</script>

<style lang="css" scoped>
.increase{
    width: 70%;
    height: 50px;
    margin: auto;
    position: absolute;
    bottom: 20px;
    left: 14%;
}

input{
    width: 100%;
    height: 44px;
    border: none;
    outline: none;
    padding: 0 30px;
}

.icon{
    width: 19px;
    height: 19px;
    position: absolute;
    top: 11px;
    left: 5px;
}
.progress{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 5%;
}
</style>