<template>
  <div class="increase">
    <div class="builtlist">
      <ul>
        <li
          v-for="(item, index) in lostlist"
          :key="index"
          @click="sta(item)"
          @mouseover="stat(index)"
          @mouseleave="resetState"
          :class="item.state ? 'bgcol' : ''"
        >
          <svg
            data-v-a22cce3a=""
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            viewBox="0 0 24 24"
          >
            <g fill="none">
              <path
                d="M2.75 18h12.5a.75.75 0 0 1 .102 1.493l-.102.007H2.75a.75.75 0 0 1-.102-1.494L2.75 18h12.5h-12.5zm0-6.5h18.5a.75.75 0 0 1 .102 1.493L21.25 13H2.75a.75.75 0 0 1-.102-1.493l.102-.007h18.5h-18.5zm0-6.497h15.5a.75.75 0 0 1 .102 1.493l-.102.007H2.75a.75.75 0 0 1-.102-1.493l.102-.007h15.5h-15.5z"
                fill="currentColor"
              ></path>
            </g>
          </svg>
          <span>{{ item.name }}</span>
          <i
            class="el-icon-delete"
            @click="del(index)"
            v-if="item.streakstate"
          ></i>
          <p class="incident" v-else="item.streakstate">
            {{ item.content }}
          </p>
        </li>
      </ul>
    </div>
    <div class="new-built" @click="add()">
      <svg
        data-v-a22cce3a=""
        xmlns="http://www.w3.org/2000/svg"
        class="icon"
        viewBox="0 0 1024 1024"
      >
        <path
          fill="currentColor"
          d="M480 480V128a32 32 0 0 1 64 0v352h352a32 32 0 1 1 0 64H544v352a32 32 0 1 1-64 0V544H128a32 32 0 0 1 0-64h352z"
        ></path>
      </svg>
      <span>新建列表</span>
    </div>
  </div>
</template>


<script>
export default {
  name: "TodolistLeftPush",

  data() {
    return {
      lostlist: [],
    };
  },

  mounted() {
    this.$bus.$on("addlist", this.addpush);
    const storedList = localStorage.getItem("lostlist");
    if (storedList) {
      this.lostlist = JSON.parse(storedList);
    }
  },

  methods: {
    addpush(msg) {
      this.lostlist.forEach((item) => {
        if (item.state) {
          item.list.push({
            id: item.list.length + 1,
            title: msg,
            state: false,
          });
        }
      });
      this.saveData();
    },
    add() {
      const newItem = {
        id: 1,
        name: "待办事项",
        content: 0,
        streakstate: false,
        detitng: false,
        state: false,
        list: [],
      };
      this.lostlist.push(newItem);
      this.saveData();
    },
    sta(item) {
      this.lostlist.forEach((items) => {
        items.state = items === item;
      });
      console.log(item.list);

      this.$bus.$emit("addpush", item.list);
    },
    turninp(index) {
      this.lostlist[index].detitng = true;
      if (this.lostlist[index].detitng === true) {
        this.$nextTick(() => {
          const inputElement = this.$refs.myinput;
          //   console.log(inputElement)
          if (inputElement) {
            inputElement[0].focus();
          }
        });
      }
    },
    del(index) {
      this.lostlist.splice(index, 1);
      this.saveData();
    },
    stat(indexs) {
      this.lostlist.forEach((item, index) => {
        if (index == indexs) {
          item.streakstate = true;
        } else {
          item.streakstate = false;
        }
      });
    },
    resetState() {
      this.lostlist.forEach((item) => {
        item.streakstate = false;
      });
    },
    saveData() {
      localStorage.setItem("lostlist", JSON.stringify(this.lostlist));
    },
  },
};
</script>

<style lang="css" scoped>
.increase {
  width: 90%;
  height: 400px;
  margin: auto;
}

.builtlist {
  width: 100%;
  height: 350px;
  overflow-x: hidden;
  overflow-x: scroll;
}

::-webkit-scrollbar {
  display: none;
}

.new-built {
  width: 17%;
  height: 30px;
  position: absolute;
  bottom: 10px;
  margin: auto;
  cursor: pointer;
}
.icon {
  width: 20px;
  height: 20px;
  float: left;
  margin-top: 5px;
  margin-left: 10px;
}
span {
  float: left;
  margin-top: 5px;
  margin-left: 10px;
}

li {
  width: 100%;
  height: 32px;
  margin-top: 10px;
}

li p {
  float: right;
  margin-right: 10px;
  margin-top: 5px;
}
.bgcol {
  background-color: rgb(231, 244, 255);
  border-radius: 10px;
}
i {
  float: right;
  cursor: pointer;
  margin-right: 10px;
  margin-top: 5px;
}
</style>